/* navigation */
.navigation {
	height:63px;
	/* Fallback for web browsers that doesn't support RGBa */
	background: rgb(255, 255, 255);
	/* RGBa with 0.6 opacity */
	background: rgba(255, 255, 255, 0.7);
	-moz-box-shadow: 2px 2x 5px #3e4559;
	-webkit-box-shadow: 2px 2px 5px #3e4559;
	box-shadow: 2px 2px 5px #3e4559;
	border:none;
	padding:0;
