.gold-ribbon::before,
.gold-ribbon::after {
    content: '';
    position: absolute;
    height: 0;
    width: 0;
    top: 0;
		border-left: 30px solid transparent;
		border-right: 30px solid transparent;
		border-top: 30px solid #fb0;
    border-bottom: 30px solid #fb0;
    z-index: 0; //Place triangles behind div
}
.gold-ribbon::before {
    left: -30px; //Half of height

}
.gold-ribbon::after {
    right: -30px; //Half of height
}
